<script>
import { Form } from "ant-design-vue";
const CollectionCreateForm = Form.create()({
  props: ["visible"],
  render() {
    const { visible, form } = this;
    const { getFieldDecorator } = form;
    return (
      <a-modal
        visible={visible}
        title="编辑镜像"
        okText="保存编辑"
        onCancel={() => {
          this.$emit("cancel");
        }}
        onOk={() => {
          this.$emit("create");
        }}
      >
        <a-form>
          <a-form-item
            labelCol={{ span: 8 }}
            wrapperCol={{ span: 14 }}
            label="名称："
          >
            {getFieldDecorator("名称", {
              rules: [
                {
                  required: true,
                  message: "请填写名称!"
                }
              ]
            })(<a-input />)}
          </a-form-item>
          <a-form-item
            labelCol={{ span: 8 }}
            wrapperCol={{ span: 14 }}
            label="描述："
          >
            {getFieldDecorator("描述", {
              rules: [
                {
                  required: true,
                  message: "请填写描述!"
                }
              ]
            })(<a-input />)}
          </a-form-item>
          <a-form-item
            labelCol={{ span: 8 }}
            wrapperCol={{ span: 14 }}
            label="镜像格式："
          >
            {getFieldDecorator("镜像格式", {
              rules: [
                {
                  required: true,
                  message: "请选择镜像格式!"
                }
              ]
            })(
              <a-select>
                <a-select-option value="jack">Jack</a-select-option>
                <a-select-option value="lucy">Lucy</a-select-option>
                <a-select-option value="Yiminghe">yiminghe</a-select-option>
              </a-select>
            )}
          </a-form-item>
          <a-form-item
            labelCol={{ span: 8 }}
            wrapperCol={{ span: 14 }}
            label="最小磁盘(GB)："
          >
            {getFieldDecorator("minDisk", {
              rules: [
                {
                  required: true,
                  message: "请填写最小磁盘!"
                }
              ]
            })(
              <a-input-number
                min={40}
                max={200}
                formatter={value => (value ? `${value} G` : "")}
                parser={value => value.replace(" G", "")}
              />
            )}
          </a-form-item>
          <a-form-item
            labelCol={{ span: 8 }}
            wrapperCol={{ span: 14 }}
            label="最低内存(GB)："
          >
            {getFieldDecorator("minMomery", {
              rules: [
                {
                  required: true,
                  message: "请填写最低内存!"
                }
              ]
            })(
              <a-input-number
                min={2}
                max={100}
                formatter={value => (value ? `${value} G` : "")}
                parser={value => value.replace(" G", "")}
              />
            )}
          </a-form-item>
          <a-form-item
            labelCol={{ span: 8 }}
            wrapperCol={{ span: 14, offset: 8 }}
            label=""
          >
            {getFieldDecorator("public", {})(<a-checkbox>公有</a-checkbox>)}
          </a-form-item>
        </a-form>
      </a-modal>
    );
  }
});

export default {
  data() {
    return {
      visible: false
    };
  },
  props: ["obj"],
  mounted() {
    const form = this.formRef.form;
    form.setFieldsValue(this.obj || {});
  },
  methods: {
    showModal() {
      this.visible = true;
    },
    handleCancel() {
      this.visible = false;
    },
    handleCreate() {
      const form = this.formRef.form;
      form.validateFields((err, values) => {
        if (err) {
          return;
        }

        console.log("Received values of form: ", values);
        form.resetFields();
        this.visible = false;
      });
    },
    saveFormRef(formRef) {
      this.formRef = formRef;
    }
  },

  render() {
    return (
      <div>
        <a-button
          type="primary"
          style="margin-right: 10px;"
          icon="edit"
          onClick={this.showModal}
        >
          编辑镜像
        </a-button>
        <CollectionCreateForm
          wrappedComponentRef={this.saveFormRef}
          visible={this.visible}
          onCancel={this.handleCancel}
          onCreate={this.handleCreate}
        />
      </div>
    );
  }
};
</script>